/**
 * FoilsTable Component
 * 
 * IMPORTANT: Column Alignment
 * - The table uses fixed column widths for consistent layout and horizontal scrolling
 * - Each column width in headerCell must match the corresponding cell width
 * - Current widths:
 *   - Regular columns (brand, model, type, series): 150px
 *   - Number columns (area, wingspan, AR, price): 100px
 *   - Link column: 100px
 * - When modifying column styles, always update both headerCell and cell styles to maintain alignment
 * - Table has a minWidth of 1200px to ensure proper layout on all screen sizes
 */

import React from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform, useWindowDimensions } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { SortField } from '../hooks/useSortedFoils';
import { colors } from '../theme/colors';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT, COLUMN_WIDTHS, getTableWidth, getContentWidth, Column } from '../constants/layout';

interface FoilsTableProps {
  foils: BaseProduct[];
  onSort: (field: SortField) => void;
  onFoilSelect: (foil: BaseProduct) => void;
  isSelected: (foil: BaseProduct) => boolean;
  sortOption: { field: SortField; ascending: boolean };
  columns: Column[];
  filters: {
    brands: string[];
    series: string[];
    type: string[];
  };
}

const DEFAULT_COLUMNS = [
  { id: 'brand', label: 'Brand', isVisible: true },
  { id: 'series', label: 'Series', isVisible: false },
  { id: 'model', label: 'Model', isVisible: true },
  { id: 'area', label: 'Area', isVisible: true },
  { id: 'wingspan', label: 'Span', isVisible: true },
  { id: 'aspectRatio', label: 'AR', isVisible: true },
  { id: 'type', label: 'Type', isVisible: true },
  { id: 'price', label: 'Price', isVisible: true },
  { id: 'link', label: 'Link', isVisible: true },
];

export const FoilsTable: React.FC<FoilsTableProps> = ({
  foils,
  onSort,
  onFoilSelect,
  isSelected,
  sortOption,
  columns,
  filters,
}) => {
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;

  // Calculate total width based on visible columns
  const tableWidth = React.useMemo(() => {
    return columns.reduce((total, column) => {
      if (column.isVisible) {
        return total + COLUMN_WIDTHS[column.id];
      }
      return total;
    }, 0);
  }, [columns]);

  const renderSortIcon = (field: SortField) => {
    const isActive = sortOption.field === field;
    
    return (
      <View style={styles.sortIconContainer}>
        <Text style={[
          styles.sortIcon,
          styles.sortIconUp,
          isActive && sortOption.ascending && styles.sortIconUpActive
        ]}>▲</Text>
        <Text style={[
          styles.sortIcon,
          styles.sortIconDown,
          isActive && !sortOption.ascending && styles.sortIconDownActive
        ]}>▼</Text>
      </View>
    );
  };

  const getHeaderTextStyle = (field: SortField) => {
    const isFiltered = 
      (field === 'brand' && filters.brands.length > 0) ||
      (field === 'series' && filters.series.length > 0) ||
      (field === 'type' && filters.type.length > 0);

    return [
      styles.headerText,
      isFiltered && styles.headerTextFiltered
    ];
  };

  const getColumnWidth = (columnId: string) => {
    return COLUMN_WIDTHS[columnId as keyof typeof COLUMN_WIDTHS] || 100;
  };

  const renderHeaderCell = (column: typeof columns[0]) => {
    if (!column.isVisible) return null;

    const commonProps = {
      style: [
        styles.headerCell,
        { width: getColumnWidth(column.id) },
        (column.id === 'brand' && filters.brands.length > 0) && styles.headerCellFiltered,
        (column.id === 'series' && filters.series.length > 0) && styles.headerCellFiltered,
        (column.id === 'type' && filters.type.length > 0) && styles.headerCellFiltered,
      ],
    };

    if (column.id === 'link') {
      return (
        <View {...commonProps}>
          <Text style={[styles.headerText, styles.linkHeaderText]}>{column.label}</Text>
        </View>
      );
    }

    return (
      <Pressable 
        {...commonProps}
        onPress={() => onSort(column.id as SortField)}
      >
        <Text style={[
          getHeaderTextStyle(column.id as SortField),
          ['area', 'wingspan', 'aspectRatio', 'price'].includes(column.id) && styles.numberHeaderText
        ]}>{column.label}</Text>
        {renderSortIcon(column.id as SortField)}
      </Pressable>
    );
  };

  const renderCell = (foil: BaseProduct, column: typeof columns[0]) => {
    if (!column.isVisible) return null;

    const commonStyle = [
      styles.cell,
      { width: getColumnWidth(column.id) },
    ];

    switch (column.id) {
      case 'brand':
        return <Text style={commonStyle}>{foil.brand}</Text>;
      case 'series':
        return <Text style={commonStyle}>{foil.series}</Text>;
      case 'model':
        return <Text style={commonStyle}>{foil.model}</Text>;
      case 'area':
        return <Text style={commonStyle}>{foil.specs.area === 0 ? '-' : foil.specs.area}</Text>;
      case 'wingspan':
        return <Text style={commonStyle}>{foil.specs.wingspan === 0 ? '-' : foil.specs.wingspan}</Text>;
      case 'aspectRatio':
        return <Text style={commonStyle}>{!foil.specs.aspectRatio || foil.specs.aspectRatio === 0 ? '-' : foil.specs.aspectRatio.toFixed(2)}</Text>;
      case 'type':
        return <Text style={commonStyle}>{foil.type}</Text>;
      case 'price':
        return <Text style={commonStyle}>{foil.price ? `$${foil.price}` : '-'}</Text>;
      case 'link':
        return (
          <View style={commonStyle}>
            {foil.sourceUrl && (
              <Pressable 
                onPress={(e) => {
                  e.stopPropagation();
                  window.open(foil.sourceUrl, '_blank');
                }}
                style={({ pressed }) => [
                  styles.linkButton,
                  pressed && styles.linkButtonPressed
                ]}
              >
                <Text style={styles.linkText}>View</Text>
              </Pressable>
            )}
          </View>
        );
      default:
        return null;
    }
  };

  return (
    <View style={styles.container}>
      <ScrollView 
        horizontal 
        showsHorizontalScrollIndicator={true}
        contentContainerStyle={[
          styles.scrollViewContent,
          { width: Math.max(tableWidth, width - (isMobile ? 0 : CONTAINER_PADDING * 2)) }
        ]}
        style={[
          styles.horizontalScrollView,
          isMobile && styles.horizontalScrollViewMobile
        ]}
      >
        <View style={[
          styles.tableContent,
          { width: tableWidth }
        ]}>
          <View style={[styles.tableHeader, { width: tableWidth }]}>
            <View style={styles.headerRow}>
              {columns.map(column => (
                <React.Fragment key={column.id}>
                  {renderHeaderCell(column)}
                </React.Fragment>
              ))}
            </View>
          </View>
          <View style={[
            styles.scrollViewContainer,
            { width: tableWidth }
          ]}>
            <ScrollView 
              style={styles.scrollView}
              showsVerticalScrollIndicator={true}
              bounces={false}
            >
              {foils.map((foil, index) => (
                <Pressable
                  key={`${foil.brand}-${foil.model}`}
                  onPress={() => onFoilSelect(foil)}
                  style={({ pressed }) => [
                    styles.row,
                    { width: tableWidth },
                    index % 2 === 0 && styles.rowEven,
                    isSelected(foil) && styles.rowSelected,
                    Platform.OS === 'web' && pressed && styles.rowHovered
                  ]}
                >
                  {columns.map(column => (
                    <React.Fragment key={column.id}>
                      {renderCell(foil, column)}
                    </React.Fragment>
                  ))}
                </Pressable>
              ))}
            </ScrollView>
          </View>
        </View>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: '100%',
    backgroundColor: colors.background.primary,
    alignItems: 'center',
  },
  horizontalScrollView: {
    flex: 1,
    width: '100%',
  },
  horizontalScrollViewMobile: {
    marginHorizontal: -CONTAINER_PADDING,
    paddingHorizontal: CONTAINER_PADDING,
  },
  scrollViewContent: {
    flexGrow: 0,
  },
  tableContent: {
    flex: 1,
    backgroundColor: colors.background.primary,
  },
  tableHeader: {
    backgroundColor: colors.background.secondary,
    borderBottomWidth: 1,
    borderBottomColor: colors.primary,
  },
  headerRow: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    borderBottomColor: colors.secondary,
  },
  headerCell: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: 12,
    borderRightWidth: 1,
    borderRightColor: colors.border,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  headerText: {
    color: colors.text.secondary,
    fontWeight: '600',
    fontSize: 16,
  },
  headerTextFiltered: {
    color: colors.primary,
  },
  row: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    borderBottomColor: colors.border,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  rowEven: {
    backgroundColor: colors.background.secondary,
  },
  rowSelected: {
    backgroundColor: `${colors.primary}15`,
  },
  rowHovered: {
    backgroundColor: `${colors.primary}10`,
  },
  cell: {
    color: colors.text.primary,
    fontSize: 14,
    padding: 12,
    borderRightWidth: 1,
    borderRightColor: colors.border,
  },
  modelCell: {
    width: 150,
  },
  typeCell: {
    width: 80,
  },
  seriesCell: {
    width: 120,
  },
  numberCell: {
    width: 80,
    textAlign: 'right',
  },
  priceCell: {
    width: 105,
    textAlign: 'right',
  },
  numberHeaderCell: {
    width: 80,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    gap: 4,
  },
  priceHeaderCell: {
    width: 105,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    gap: 4,
  },
  numberHeaderText: {
    textAlign: 'left',
    flex: 1,
  },
  linkCell: {
    width: 80,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 0,
    borderTopRightRadius: Platform.OS === 'web' ? 0 : 8,
    borderBottomRightRadius: Platform.OS === 'web' ? 0 : 8,
  },
  linkHeaderCell: {
    width: 80,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 0,
    borderTopRightRadius: Platform.OS === 'web' ? 0 : 8,
  },
  linkHeaderText: {
    textAlign: 'center',
  },
  linkButton: {
    backgroundColor: colors.primary,
    paddingHorizontal: 8,
    paddingVertical: 4,
    borderRadius: 4,
    minWidth: 44,
    alignItems: 'center',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  linkButtonPressed: {
    opacity: 0.8,
  },
  linkText: {
    color: colors.background.primary,
    fontSize: 12,
    fontWeight: '600',
  },
  sortIconContainer: {
    flexDirection: 'column',
    alignItems: 'center',
    height: 28,
    justifyContent: 'center',
    width: 16,
    gap: 2,
  },
  sortIcon: {
    color: colors.text.muted,
    fontSize: 12,
    width: 12,
    height: 10,
    lineHeight: 10,
    textAlign: 'center',
  },
  sortIconUp: {
    marginBottom: 0,
  },
  sortIconDown: {
    marginTop: 0,
  },
  sortIconUpActive: {
    color: colors.primary,
    fontSize: 13,
  },
  sortIconDownActive: {
    color: colors.primary,
    fontSize: 13,
  },
  scrollViewContainer: {
    flex: 1,
  },
  scrollView: {
    flex: 1,
  },
  headerCellFiltered: {
    borderBottomWidth: 2,
    borderBottomColor: colors.primary,
  },
});
